import { LatLngLiteral } from 'leaflet'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { MapContainer, Marker, Polyline, TileLayer } from 'react-leaflet'
import { RouteComponentProps, useLocation } from 'react-router'
import { AdminItemFieldRegistro, AdminModalRuteo, Page } from '../../components'
import LinkButton from '../../components/LinkButton'
import moment from 'moment'

import LoaderAdmin from '../../components/LoaderAdmin'
import { PEDIDO_ESTADOS, PEDIDO_ESTADOS_RUTEO_STR, PEDIDO_ESTADOS_STR, RUTAS } from '../../constants'
import { fechahora_fmt, plata_fmt, string2leaflet_location } from '../../helpers'
import {
  centroItinerarios, DetailViewProps,
  ICONOS_GLOBO,
  itinerario_marker,
  itinerario_popup,
  useApiToaster,
  useToaster,
} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { getEstadoOrdenVentaDisplay } from '../../models/types/Pedido'
import { ItinerarioApi, OrdenVenta, Pedido } from '../../services/api'
import { DATE_FORMAT } from '../../utils/date'
import './AdminClienteDetail.css'
import { Button } from 'react-bootstrap'

export const AdminPedidoDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const [Pedido, setPedido] = useState<Pedido>()

  const [Loading, setLoading] = useState(true)

  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 })
  const [Itinerarios, setItinerarios] = useState([])
  const [Transportista, setTransportista] = useState(null as any)
  const [Polylines, setPolyline] = useState([[-57.63591, -25.30066]])
  const [ModalRuteo, setModalRuteo] = useState(false)

  const apiToaster = useApiToaster()
  const addToast = useToaster()
  const { search } = useLocation()

  const idPedido = match.params.id

  const setup = () => {
    obtenerPedido(idPedido)
  }

  React.useEffect(() => {
    setup()
  }, [idPedido])

  React.useEffect(() => {
    const query = new URLSearchParams(search)
    if (query.get('r') === 'true') {
      obtenerPedido(idPedido)
      history.push(RUTAS.ADMIN_PEDIDO_DETAIL(idPedido))
    }
  }, [search])

  const obtenerPedido = (idPedido: number) => {
    setLoading(true)
    rootStore.environment.api.adminGetPedido(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setLoading(false)
        setPedido(result.pedido)
      }
    })
  }

  const rastreoLive = (pedido: Pedido) => {
    if (pedido.estado >= PEDIDO_ESTADOS.pagado) {
      refrescarUbicacion(pedido.id)
      if (pedido.estado < PEDIDO_ESTADOS.entregado) {
        let timerId = setInterval(() => {
          refrescarUbicacion(pedido.id)
        }, 10000)
        // destructor
        return () => clearInterval(timerId)
      }
    }
    return null
  }

  React.useEffect(() => {
    setLoading(false)
    if (Pedido) {
      const timer = rastreoLive(Pedido)
      if (timer) return timer // destructor
    }
  }, [Pedido])

  const ubicacionTransportista = () => {
    if (Transportista) {
      return Transportista.split(',')
    } else return Center
  }

  const refrescarUbicacion = (idPedido: number) => {
    rootStore.environment.api.rastrearPedido(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setItinerarios(result.data.itinerarios)
        setTransportista(result.data.transportista)
        let convertidos = result.data.itinerarios.map((i: ItinerarioApi) => string2leaflet_location(i.ubicacion))
        setCenter(centroItinerarios(convertidos))
        setPolyline(convertidos.map((ubi: LatLngLiteral) => [ubi.lat, ubi.lng]))
      }
    })
  }

  return (
    <Page login_required admin>
      <LoaderAdmin visible={Loading} />
      {!!Pedido && ModalRuteo && (
        <AdminModalRuteo pedido={Pedido} visible={ModalRuteo} setVisible={() => setModalRuteo(false)} />
      )}
      {!Loading && Pedido && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">Pedido {Pedido.referencia}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a href={RUTAS.ADMIN_PEDIDOS}>Pedidos</a>
                  </li>
                  <li className="breadcrumb-item active">{Pedido.referencia}</li>
                </ol>
              </div>
            </div>
            <div className="card">
              <div className="card-body">
                <table className={'table'}>
                  <tbody>
                    <tr>
                      <td>Referencia</td>
                      <td>{Pedido.referencia}</td>
                    </tr>
                    <tr>
                      <td>Fecha</td>
                      <td>{fechahora_fmt(Pedido.fecha)}</td>
                    </tr>
                    <tr>
                      <td>Fecha de aceptación</td>
                      <td>{Pedido.fecha_aceptado ? fechahora_fmt(Pedido.fecha_aceptado) : 'PENDIENTE'}</td>
                    </tr>
                    <tr>
                      <td>Fecha de envío</td>
                      <td>{Pedido.fecha_envio ? fechahora_fmt(Pedido.fecha_envio) : 'PENDIENTE'}</td>
                    </tr>
                    <tr>
                      <td>Fecha de entrega</td>
                      <td>{Pedido.fecha_entrega ? fechahora_fmt(Pedido.fecha_entrega) : 'PENDIENTE'}</td>
                    </tr>
                    <tr>
                      <td>Cliente</td>
                      <td>
                        <LinkButton href={RUTAS.ADMIN_CLIENTE_DETAIL(Pedido.usuario.id)}>
                          {Pedido.usuario.first_name} {Pedido.usuario.last_name} - {Pedido.usuario.email}{' '}
                        </LinkButton>
                      </td>
                    </tr>
                    <tr>
                      <td>Delivery</td>
                      <td>{plata_fmt(Pedido.envio)}</td>
                    </tr>
                    <tr>
                      <td>Total</td>
                      <td>{plata_fmt(Pedido.total)}</td>
                    </tr>
                    <tr>
                      <td>Tipo de compra</td>
                      <td>{Pedido.precio_mejorado ? 'Mejorada' : 'Express'}</td>
                    </tr>
                    <tr>
                      <td>Estado</td>
                      <td>{PEDIDO_ESTADOS_STR[Pedido.estado]}</td>
                    </tr>
                    {Pedido.estado_ruteo && (
                      <tr>
                        <td>Estado ruteo</td>
                        <td>{PEDIDO_ESTADOS_RUTEO_STR[Pedido.estado_ruteo]}</td>
                      </tr>
                    )}
                    {Pedido.info_fallo_ruteo && (
                      <tr>
                        <td>Razon fallo ruteo</td>
                        <td>{Pedido.info_fallo_ruteo}</td>
                      </tr>
                    )}
                    {Pedido.hoja_ruta && (
                      <tr>
                        <td>Hoja de ruta</td>
                        <td>
                          <LinkButton href={RUTAS.ADMIN_HOJA_RUTA_DETAIL(Pedido.hoja_ruta)}>Ver</LinkButton>
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
            <h4>Detalle</h4>
            <div className="card">
              <div className="card-body">
                <div className={'table-responsive'}></div>
                <table className={'table'}>
                  <thead>
                    <tr>
                      <th>Producto</th>
                      <th>Cantidad</th>
                      <th>Precio</th>
                      <th>Subtotal</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Pedido.productos.map((pedidoDetalle, idx) => {
                      return (
                        <tr key={'producto' + idx}>
                          <td>
                            <LinkButton href={RUTAS.ADMIN_PRODUCTO_DETAILD(pedidoDetalle.producto.id)}>
                              {pedidoDetalle.producto.nombre}
                            </LinkButton>
                          </td>
                          <td>{pedidoDetalle.cantidad}</td>
                          <td>{plata_fmt(pedidoDetalle.precio)}</td>
                          <td>{plata_fmt(pedidoDetalle.total)}</td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </div>
            </div>
            <h4>Ordenes de venta</h4>
            <div className="card">
              <div className="card-body">
                <div className={'table-responsive'}></div>
                <table className={'table'}>
                  <thead>
                    <tr>
                      <th>Referencia</th>
                      <th>Proveedor</th>
                      <th>Estado</th>
                      <th>Fecha</th>
                      <th>Fecha de retiro</th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {Pedido.ordenes_venta &&
                      Pedido.ordenes_venta.map((orden, idx) => {
                        return (
                          <tr key={'orden' + idx}>
                            <td>
                              <LinkButton href={RUTAS.ADMIN_ORDEN_VENTA_DETAIL(orden.id)}>
                                {orden.referencia}
                              </LinkButton>
                            </td>
                            <td>{orden.fabricante}</td>
                            <td>{getEstadoOrdenVentaDisplay(orden.estado)}</td>
                            <td>{orden.fecha ? fechahora_fmt(orden.fecha) : '-'}</td>
                            <td>{orden.fecha_retiro ? fechahora_fmt(orden.fecha_retiro) : '-'}</td>
                          </tr>
                        )
                      })}
                  </tbody>
                </table>
              </div>
            </div>
            {!!Pedido.ordenes_venta &&
              Pedido.ordenes_venta.length > 0 &&
              !!Pedido.estado_ruteo &&
              Pedido.estado_ruteo > 40 && (
                <>
                  <Button className={'btn btn-light ml-3 mb-3'} onClick={() => setModalRuteo(true)}>
                    Rutear Pedido
                  </Button>
                </>
              )}

            {Itinerarios && Itinerarios.length > 0 && (
              <div style={{ height: 500 }} className={'card'}>
                <MapContainer
                  zoom={13}
                  center={Center}
                  style={{ height: 500, width: '100%' }}
                  attributionControl={false}
                >
                  <TileLayer
                    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.osm.org/{z}/{x}/{y}.png"
                  />
                  <Polyline
                    // @ts-ignore
                    positions={Polylines}
                  />
                  {Itinerarios &&
                    Itinerarios.map((itinerario: ItinerarioApi) =>
                      itinerario_marker(itinerario, itinerario_popup(itinerario)),
                    )}
                  {Transportista && (
                    <Marker position={ubicacionTransportista()} icon={ICONOS_GLOBO.transportista2}></Marker>
                  )}
                </MapContainer>
              </div>
            )}
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminPedidoDetail
